import React, { Component } from 'react';

import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import { Icon, Menu } from 'antd';

import BasePage from './pages/BasePage';
import GeneralPage from './pages/GeneralPage';
// import UsersPage from './pages/UsersPage';
import TypesSolidWastePage from './pages/TypesSolidWastePage';
import SolidWasteCollectedPage from './pages/SolidWasteCollectedPage';
import AnalyzeCollectedWastesPage from './pages/AnalyzeCollectedWastesPage';
import ReportPage from './pages/ReportPage';


export default class Routes extends Component {
    constructor(props) {
        super(props);
        this.state = {
            titlePage: 'Viso Geral'
        }
    }

    render = () => (
        <Router>
            <BasePage
                menu={
                    <Menu mode="inline" style={{ zIndex: '999' }}>
                        <Menu.Item key="1" onClick={() => {this.setState({titlePage: 'Viso Geral'})}}>
                            <Icon type="home" />
                            <Link to="/" className="nav-text">Viso Geral</Link>
                        </Menu.Item>
                        {/* <Menu.Item key="2" onClick={() => {this.setState({titlePage: 'Usurios'})}}>
                            <Icon type="user" />
                            <Link to="/usuarios" className="nav-text">Usurios</Link>
                        </Menu.Item> */}
                        <Menu.Item key="3" onClick={() => {this.setState({titlePage: 'Tipos de Resduos Slidos'})}}>
                            <Icon type="tag" />
                            <Link to="/tipos-residuos-solidos" className="nav-text">Tipos de Resduos Slidos</Link>
                        </Menu.Item>
                        <Menu.Item key="4" onClick={() => {this.setState({titlePage: 'Coletas'})}}>
                            <Icon type="delete" />
                            <Link to="/coletas" className="nav-text">Coletas</Link>
                        </Menu.Item>
                        <Menu.Item key="5" onClick={() => {this.setState({titlePage: 'Anlises'})}}>
                            <Icon type="line-chart" />
                            <Link to="/analises" className="nav-text">Anlises</Link>
                        </Menu.Item>
                        <Menu.Item key="6" onClick={() => {this.setState({titlePage: 'Relatrios'})}}>
                            <Icon type="file-text" />
                            <Link to="/relatorios" className="nav-text">Relatrios</Link>
                        </Menu.Item>
                    </Menu>
                }

                content={
                    <>
                        <Route exact path="/" component={GeneralPage} />
                        {/* <Route exact path="/usuarios" component={UsersPage} /> */}
                        <Route exact path="/tipos-residuos-solidos" component={TypesSolidWastePage} />
                        <Route exact path="/coletas" component={SolidWasteCollectedPage} />
                        <Route exact path="/analises" component={AnalyzeCollectedWastesPage} />
                        <Route exact path="/relatorios" component={ReportPage} />
                        {/* <Route exact path="/analises/s" component={AnalyzeCollectedWastesPage} /> */}
                    </>
                }

                titlePage={this.state.titlePage}
            />
        </Router>
    );
}